    return {
      info: "<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\" align=\"center\" style=\"text-align:center;\">\r\n\t<b>NftList</b><b>参投告知说明<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" align=\"center\" style=\"text-align:center;\">\r\n\tNTS节点抢购机制<span></span> \r\n</p>\r\n<p class=\"MsoListParagraph\" style=\"margin-left:18.0pt;text-indent:-18.0pt;\">\r\n\t<b>1.&nbsp;&nbsp;</b><b>发售额度<span>:</span></b> \r\n</p>\r\n<p class=\"MsoListParagraph\" style=\"margin-left:36.0pt;text-indent:0cm;\">\r\n\tNftList平台唯一代币<span>NTS</span>，恒量发行<span>9000</span>万枚。<span>5%</span>节点预售<span>450</span>万枚。 限量发售，抢完为止。<span></span> \r\n</p>\r\n<p class=\"MsoListParagraph\" style=\"margin-left:18.0pt;text-indent:-18.0pt;\">\r\n\t<b>2.&nbsp;&nbsp;</b><b>抢购收益：<span></span></b> \r\n</p>\r\n<p class=\"MsoListParagraph\" style=\"margin-left:36.0pt;text-indent:0cm;\">\r\n\tNTS上线前节点抢购价为<span>0.3USDT</span>，上线后销售价为<span>0.45USDT</span>。<span></span> \r\n</p>\r\n<p class=\"MsoListParagraph\" style=\"margin-left:18.0pt;text-indent:-18.0pt;\">\r\n\t<b>3.&nbsp;&nbsp;</b><b>抢购额度：<span></span></b> \r\n</p>\r\n<p class=\"MsoListParagraph\" style=\"margin-left:36.0pt;text-indent:0cm;\">\r\n\t每位参投用户的节点抢购额度，定额为<span>20000</span>枚<span>/6000USDT </span>。抢购成功即升级为节点身份。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>4.&nbsp;&nbsp;</b><b>释放规则：</b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:91.0pt;text-indent:-70.0pt;\">\r\n\t（<span>1</span>）推广加速释放，抢购的<span>NTS</span>不受限制用于个人及社区的投资算力与推广算力计划。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:52.5pt;text-indent:-42.0pt;\">\r\n\t（<span>2</span>）投资释放，抢购的<span>NTS</span>，若不参与算力计划，可按每天<span>0.5%</span>进行线性释放。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:52.5pt;text-indent:-42.0pt;\">\r\n\t（<span>3</span>）根据社区参与通缩算力计划达<span>60000</span>枚全部释放。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>5.&nbsp;&nbsp;</b><b>推广奖励：</b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:52.5pt;text-indent:-42.0pt;\">\r\n\t&nbsp;&nbsp;&nbsp;&nbsp; 每位节点推荐一名<a name=\"_Hlk82170972\"></a>抢购成功的节点，获<span>2000</span>枚<span>NTS</span>的节点奖励。每位节点推荐<span>5</span>名抢购成功的节点，即可升级为超级节点。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>6.&nbsp;&nbsp;</b><b>节点分红：<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:56.0pt;text-indent:-56.0pt;\">\r\n\t&nbsp;&nbsp;&nbsp;&nbsp; （<span>1</span>）节点与超级节点永久享受算力计划提币手续费<span>10%</span>的节点分红。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:42.0pt;text-indent:-42.0pt;\">\r\n\t&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; （<span>2</span>）超级节点永久享受算力计划提币手续费<span>5%</span>的超级节点分红。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;margin-left:42pt;text-indent:-42pt;\">\r\n\t<b>NftList</b><b>的算力分享奖励机制</b><b></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<a name=\"_Hlk81906555\"></a><b>一、</b><b>投资算力（全球公排回馈） </b> \r\n</p>\r\n<table class=\"MsoTableGrid\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\" style=\"border-collapse:collapse;border:none;\">\r\n\t<tbody>\r\n\t\t<tr>\r\n\t\t\t<td width=\"92\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\"><span style=\"font-size:14px;\">投资额度</span><span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">USDT</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"92\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\"><span style=\"font-size:14px;\">获取算力</span><span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\"><span style=\"font-size:14px;\">额度</span><span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"92\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<