ndowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:14.0pt;\"><span style=\"font-size:14px;\">奖励额度</span><span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">20%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">5%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">3%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">3%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">5%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">2%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"69\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\" style=\"text-indent:14.0pt;\">\r\n\t\t\t\t\t<span style=\"font-size:14px;\">2%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t</tbody>\r\n</table>\r\n<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>三、分享奖励机制注释：<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:18.0pt;text-indent:-18.0pt;\">\r\n\t1、 全球公排回馈中获得上下<span>20</span>级的<span>1%</span>奖励，是以对方购买算力的额度为奖励基数。根据购买算力额度不同，其收益享受的提现与复投额度不同，复投的额度与购买算力享受同等回馈。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:18.0pt;text-indent:-18.0pt;\">\r\n\t2、参与全球公排回馈和直接分享奖励获赠代币奖励外，额外获得对等算力奖励参与通缩矿池挖矿。参与通缩矿池挖矿的获赠算力产生的收益对等获赠算力按<span>1U=1</span>算力相互抵消。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:18.0pt;text-indent:-18.0pt;\">\r\n\t3、全球公排回馈中没完成最低<span>5</span>个直推任务的，收益最高只能按<span>40%</span>提现，<span>60%</span>复投执行。并在直接分享奖励中没有购买<span>400U</span>算力和完成<span>5</span>个直推的最高只能享受到第<span>4</span>代的奖励。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;\">\r\n\t<b>NftList</b><b>的<span>DEFI</span>矿池收益机制<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>一、&nbsp;&nbsp;&nbsp;&nbsp; </b><b>通缩矿池收益：</b>NTS配比<span>6000</span>万枚<b><span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>1、&nbsp;&nbsp;</b><b>通缩矿池产币量<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t（全球<a name=\"_Hlk81910170\"></a>投资算力总额<span>+</span>投资算力复投总额）×<span>20%=</span><a name=\"_Hlk81915947\"></a>通缩矿池产币量<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>2、&nbsp;</b><b>个人通缩矿池收益<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t个人推广算力<a name=\"_Hlk81918556\"></a>÷<span>(</span>全球投资算力<span>+</span>全球推广算力<span>) </span>×通缩矿池产币量<span>=</span>个人通缩矿池收益<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<a name=\"_Hlk81914791\"></a> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>3、&nbsp;&nbsp;</b><b>通缩矿池收益注释：<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t（<span>1</span>）个人通缩矿池收益为日结，所有数据核算标准为<span>24</span>小时量。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t（<span>2</span>）个人推广算力特定指首次投资算力的额度，不包含复投额度。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t（<span>3</span>）通缩矿池产币量将全额销毁，让<span>NTS</span>实现极致通缩的效应。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;\">\r\n\t<br />\r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>二、质押矿池收益：</b>NTS配比<span>2000</span>万枚<span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>1</b><b>、质押算力<span></span></b> \r\n</p>\r\n<table class=\"MsoTableGrid\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\" style=\"border-collapse:collapse;border:none;\">\r\n\t<tbody>\r\n\t\t<tr>\r\n\t\t\t<td width=\"102\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">NTS</span><span style=\"font-size:12.0pt;\">比例<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"104\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">主流币<span>+20%</span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">主流币<span>+40%</span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">主流币<span>+60%</span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">NTS100%</span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td width=\"102\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">算力配比<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"104\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=1</span><span style=\"font-size:12.0pt;\">算力<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=1.5</span><span style=\"font-size:12.0pt;\">算力<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=2</span><span style=\"font-size:12.0pt;\">算力<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t\t<td width=\"100\" valign=\"top\" style=\"border:solid windowtext 1.0pt;\">\r\n\t\t\t\t<p class=\"MsoNormal\">\r\n\t\t\t\t\t<span style=\"font-size:12.0pt;\">1U=3</span><span style=\"font-size:12.0pt;\">算力<span></span></span> \r\n\t\t\t\t</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t</tbody>\r\n</table>\r\n<p class=\"MsoNormal\">\r\n\t<b>2</b><b>、质押矿池产币量<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;\">\r\n\t质押矿池每日最低产币量为<span>2</span>万枚。每日质押算力每达到<span>1000</span>万，产币量增加<span>2</span>万枚，增加算力不足<span>1000</span>万，不增加产币量； 每日算力不足<span>1000</span>万按照<span>500</span>：<span>1</span>的比例下调产币的分配量。当日未分配的币全部销毁。 <span></span> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t<b>3</b><b>、个人质押矿池收益<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\">\r\n\t个人质押算力÷全球质押算力×质押矿池产币量<span>=</span>个人质押矿池收益<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:36.0pt;text-indent:-36.0pt;\">\r\n\t<b>4、</b><b>质押矿池收益注释：<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:72.0pt;text-indent:-36.0pt;\">\r\n\t（1）质押矿池质押的代币，可随时结算收益，自由进出。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:72.0pt;text-indent:-36.0pt;\">\r\n\t（2）提币手续费为<span>10%</span>。据分配如下：<span>50%</span>销毁，<span>10%</span>节点，<span>5%</span>超级节点，<span>35%</span>基金会。<span></span> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;\">\r\n\t<b>&nbsp;&nbsp;</b><b>本参投告知说明，最终解释权归<span>NftList Fund</span>所有。<span></span></b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"text-align:center;text-indent:42pt;\">\r\n\t<b>&nbsp;</b><b>我已详细阅读并认同以上<span>NftList</span>参投告知说明 </b> \r\n</p>\r\n<p class=\"MsoNormal\" style=\"margin-left:52.5pt;text-indent:-42.0pt;\">\r\n\t<b></b> \r\n</p>"
    }
  },
}
</script>

